	module.exports = {"data":{"talkJson":{"description":"Source: http://tech.yandex.ru/events/cpp-party/june-msk/talks/1954/\n\nI especially like 36:58\n\nThe Vasa was a 17th-century Swedish warship which suffered such feature creep during construction that it sank shortly after leaving the harbour on its maiden voyage. In the early 1990s, the C++ standardisation committee adopted the Vasa as a cautionary tale, discouraging prospective language extensions with \"Remember the Vasa!\" Yet C++ continued to grow, and by the time C++ was standardised, its complexity made the Vasa look like a rowboat.\n\nThe Vasa sank, however, while C++ cruised, and it looks likely to continue doing so even as the latest revised standards (C++11 and C++14) add dozens of new features, each with its own idiosyncrasies. Clearly, C++ has gotten some important things right. In this talk, Scott Meyers considers the lessons to be learned from the ongoing success of a complex programming language that's over 30 years old, yet very much alive and kicking.","meta":{"duration":"PT1H7M3S"},"presenter":{"name":null},"slug":"why-c-sails-when-the-vasa-sank","source":{"id":"ltCgzYcpFUI","provider":"youtube"},"tag":["c++","C++11","C++14","scott meyers","C++ and beyond","cppcon","herb sutter","gcc","microsoft","Bjarne Stroustrup (Author)","effective C++","Vasa (Ship)"],"title":"Why C++ Sails When the Vasa Sank"},"allTalkJson":{"edges":[{"node":{"description":"Day 1 - C++ has powerful rules for dealing with low-level program structure.  Before a program is ever executed, the compiler determines valuable information about every expression in the source code.  The compiler understands exactly how long each object's resources will be needed (lifetime), whether each expression refers to an object that the program has no other way of accessing (rvalueness), and what operations can be performed on each object (type).  Using examples from C++98 through C++14, this presentation will demonstrate how to write code that works with the compiler's knowledge to increase robustness, efficiency, and clarity.  This presentation will also demonstrate the horrible things that happen when programmers think they can do tasks that are better left to compilers.","meta":{"duration":"PT1H17M"},"presenter":{"name":null},"source":{"id":"AKtHxKJRwp4","provider":"youtube"},"slug":"dont-help-the-compiler","tag":["c++","goingnative","goingnative 2013"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/AKtHxKJRwp4/default.jpg","height":90,"width":120}},"title":"Don't Help the Compiler"}},{"node":{"description":"code::dive conference 2014 - Nokia Wrocław\nhttp://codedive.pl/","meta":{"duration":"PT1H12M52S"},"presenter":{"name":null},"source":{"id":"J-tA17slViE","provider":"youtube"},"slug":"codedive-conference-2014-scott-meyers-support-for-embedded-programming-in-c11-and-c14","tag":["Nokia Wrocław","Scott Meyers:","code::dive","code::dive conference 2014","C++11","C++14"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/J-tA17slViE/default.jpg","height":90,"width":120}},"title":"code::dive conference 2014 - Scott Meyers: Support for Embedded Programming in C++11 and C++14"}},{"node":{"description":"Day 1 - Contemporary computer architectures make it possible for slow code to work reasonably well. They also make it difficult to write really fast code that exploits the CPU amenities to their fullest. And the smart money is on fast code—we're running out of cool things to do with slow code, and the battle will be on doing really interesting and challenging things at the envelope of what the computing fabric endures.\n\nSo how to write quick code, quickly? Turns out it's quite difficult because today's complex architectures defy simple rules to be applied everywhere. It is not uncommon that innocuous high-level artifacts have a surprisingly high impact on the bottom line of an application's run time (and power consumed).\n\nThis talk is an attempt to set forth a few pieces of tactical advice for writing quick code in C++. Applying these is not guaranteed to produce optimal code, but is likely to put it reasonably within the ballpark. \n\nThese tips are based on practical experience but also motivated by the inner workings of modern CPUs.","meta":{"duration":"PT1H11M37S"},"presenter":{"name":null},"source":{"id":"ea5DiCg8HOY","provider":"youtube"},"slug":"writing-quick-code-in-c-quickly","tag":["c++","goingnative","goingnative 2013"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/ea5DiCg8HOY/default.jpg","height":90,"width":120}},"title":"Writing Quick Code in C++, Quickly"}},{"node":{"description":" ","meta":{"duration":"PT46M47S"},"presenter":{"name":null},"source":{"id":"sajBj_eiH10","provider":"youtube"},"slug":"using-c11-to-speed-up-your-qt-5-programs-marc-mutz","tag":["Using","C++11","to","speed","up","your","Qt","5","programs","Marc","Mutz"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/sajBj_eiH10/default.jpg","height":90,"width":120}},"title":"Using C++11 to speed up your Qt 5 programs   Marc Mutz"}}]}},"pathContext":{"slug":"why-c-sails-when-the-vasa-sank","tags":["c++","C++11","C++14","scott meyers","C++ and beyond","cppcon","herb sutter","gcc","microsoft","Bjarne Stroustrup (Author)","effective C++","Vasa (Ship)"]}}

